<template>
    <div
        @click="goPigActivity"
        class="pig">
        <div class="label">
            <p class="sub-title">喂食300天，活猪邮到家</p>
            <p class="title">您已喂养<span>{{ day }}</span>天</p>
            <p class="tip">连续喂养15天抽取节气奖励</p>
        </div>
        <div :class="[ 'btn', `btn-pig-${status}`]" />
    </div>
</template>

<script>
import { openCommonWebViewByPath } from '@/utils/jsApiV2/client';
import config from '@/config';

const debounce = require('lodash.debounce');

export default {
    name: 'PigTask',
    data() {
        return {
            statusMap: Object.freeze({
                '-1': '立即领养',
                0: '立即喂食',
                1: '已喂食',
            }),
        };
    },
    props: {
        day: { type: Number, default: 0 },
        status: { type: String, default: '-1' },
    },
    methods: {
        goPigActivity: debounce(function goPigActivity() {
            this.$o2o({
                e_c: '任务',
                e_a: '点击',
                e_n: '送猪活动',
                e_x: {
                    status: this.statusMap[this.status] || '立即领养',
                },
            }, () => {
                openCommonWebViewByPath('/mobile-v2/act/sign-pig');
            });
        }, config.debounceWait),
    },
};
</script>

