<template>
    <van-swipe
        class="swipe"
        :duration="500"
        :autoplay="2500"
        @change="swipeChange"
        ref="swipe"
    >
        <van-swipe-item
            class="item"
            v-for="item in banners"
            :key="item.icon"
        >
            <ImageLoader
                class="img"
                :is-big="true"
                :src="item.icon"
                :c-style="{width: '100%', height: '100%'}"
                @click="open(item)"
            />
        </van-swipe-item>
    </van-swipe>
</template>

<script>
import qs from 'qs';
import ImageLoader from '@/components/ImageLoader';
import { openCommonWebView, getUserAccountInfo, goLogin } from '@/utils/jsApiV2/client';
import { inViewport } from '@/utils/dom';
import { addCustomEventListener } from '@/utils/event';

const debounce = require('lodash.debounce');

export default {
    name: 'BannerSwipe',
    components: {
        ImageLoader,
    },
    props: {
        banners: { type: Array, default: () => [] },
    },
    methods: {
        swipeChange(index) {
            console.log(this.$refs.swipe.$el);

            if (inViewport(this.$refs.swipe.$el)) {
                this.expose(this.banners[index].title);
            }
        },
        open: debounce(function open(item) {
            const logined = getUserAccountInfo().user_id;
            const query = qs.parse(item.url.split('?')[1]);

            this.$o2o({
                e_c: 'banner',
                e_a: '点击',
                e_n: '赚金币banner点击',
                e_x: {
                    bannerTitle: item.title,
                },
            });

            if (!logined && query.login) {
                return goLogin();
            }

            if (item.url.indexOf('bikan://') > -1) {
                location.href = item.url;

                return true;
            }

            return openCommonWebView(item.url, item.title || '');
        }, 200),
        expose(bannerTitle) {
            this.$o2o({
                e_c: 'banner',
                e_a: '曝光',
                e_n: '赚金币banner曝光',
                e_x: {
                    bannerTitle,
                },
            });
        },
    },
    watch: {
        banners(val) {
            if (val[0]) {
                this.expose(val[0].title);
            }
        },
    },
    created() {
        addCustomEventListener('focus', () => {
            console.log('focus');
            if (this.banners[0]) {
                this.expose(this.banners[0].title);
            }
        });

        // window.addEventListener('scroll', throttle(() => {
        //     console.log(this.$refs.swipe.$el);

        //     if (this.banners[0] && inViewport(this.$refs.swipe.$el)) {
        //         this.expose(this.banners[0].title);
        //     }
        // }), 500);
    },
};
</script>

<style scoped>
.swipe {
    min-height: 100px;
    height: 100%;

    /* border-radius: 7px; */

    overflow: hidden;
    position: relative;
}

.item {
    min-height: 100px;
    height: 100%;
    width: 100%;
    float: left;
}

.img {
    min-height: 100px;
    height: 100%;
    width: 100%;
    object-fit: cover;
}
</style>
