<template>
    <div class="list">
        <div
            v-for="task in taskList"
            :key="task.id"
            class="item">
            <component
                :class="{ 'customize-expose': isAdTask(task) }"
                :is="getTaskComponent(task)"
                @download="handleDownload"
                @award="handleAward"
                @finish="handleFinish"
                v-bind="{
                    isNovice,
                    isNewNoviceTask,
                    noviceTaskDay,
                    signedDays,
                    awardCount: getAwardCount(task),
                    awardType: getAwardType(task),
                    task,
                }"
                :data-expose="isAdTask(task) ? JSON.stringify({
                    type: 'VIEW',
                    ex: task.ex,
            }) : '{}'" />
        </div>
    </div>
</template>

<script>
import { getTimeSpentToday } from '@/utils/jsApiV2/client';
import { addCustomEventListener } from '@/utils/event';
import adTracker from '@/utils/adTracker';
import { get, remove } from '@/utils/cache';
import { cachedAdTaskKey } from './config';
import TaskListItem from './TaskListItem';
import TaskListAdItem from './TaskListAdItem';
import TaskListTipItem from './TaskListTipItem';

export default {
    name: 'TaskList',
    components: {
        TaskListItem,
        TaskListAdItem,
        TaskListTipItem,
    },
    props: {
        isNovice: { type: Boolean, default: false },
        isNewNoviceTask: { type: Boolean, default: false },
        noviceTaskDay: { type: Number, default: 1 },
        signedDays: { type: Number, default: 0 },
        task: { type: Array, default: () => [] },
    },
    data() {
        return {
            taskList: [],
            adTaskCategory: 7,
            isDownloading: false,
            adIndex: 4,
        };
    },
    watch: {
        task: {
            handler(newVal) {
                const val = this.restoreAd(newVal, this.taskList);

                this.taskList = this.getFormattedTaskList(val);
            },
            immediate: true,
        },
    },
    created() {
        // 任务页面获得焦点后更新taskList
        addCustomEventListener('focus', () => {
            this.taskList = this.getFormattedTaskList(this.taskList);
        });

        this.$expose(window, adTracker);
    },
    methods: {
        getTaskComponent(task) {
            switch (task.category) {
            case 7:
                return 'TaskListAdItem';
            case 8:
            case 9:
                return 'TaskListTipItem';
            default:
                return 'TaskListItem';
            }
        },
        handleDownload() {
            this.isDownloading = true;
        },
        handleFinish() {
            this.isDownloading = false;
        },
        handleAward(taskId) {
            this.taskList.forEach((task) => {
                if (task.id === taskId) {
                    task.status = 'AWARD'; // eslint-disable-line
                }
            });
        },
        restoreAd(newTaskList, oldTaskList) {
            const val = newTaskList;
            const cachedAdTask = JSON.parse(get(cachedAdTaskKey));
            let ad;

            if (this.isDownloading) {
                ad = cachedAdTask || oldTaskList[this.adIndex];

                if (ad.adId && !newTaskList[this.adIndex].adId) {
                    val.splice(this.adIndex, 0, ad);
                }
            } else if (cachedAdTask) {
                remove(cachedAdTaskKey);
            }

            return val;
        },
        getAwardCount(item) {
            const awardType = this.getAwardType(item);

            return this.getFormattedAward(item, awardType);
        },
        getAwardType({ awardType }) {
            return awardType && (awardType.toLowerCase() || 'coin');
        },
        getFormattedAward(item, awardType) {
            const awardAmount = this.getAwardAmount(item, awardType);

            if (awardType === 'cash') {
                return this.formatCash(awardAmount);
            }

            return awardAmount;
        },
        formatCash(awardAmount) {
            return `+${awardAmount / 100}元`;
        },
        getAwardAmount(item, awardType = 'coin') {
            return item[awardType];
        },
        getFormattedTaskList(tasks) {
            return tasks.reduce((res, task) => {
                switch (task.category) {
                // 在线时长任务
                case 1:
                    res.push(this.addAppUseTime(task));
                    return res;
                // 广告下载任务
                case 7:
                    return this.filterAdTask(res, task);
                default:
                    res.push(task);
                    return res;
                }
            }, []);
        },
        filterAdTask(taskList, adTask) {
            // 存在adId视为一个正常的广告
            if (adTask.adId) {
                taskList.push(adTask);
            }

            return taskList;
        },
        addAppUseTime(task) {
            const appUseTime = this.getAppUseTime();

            return {
                ...task,
                appUseTime,
                status: this.getAppUseTimeTaskStatus(task, appUseTime),
            };
        },
        getAppUseTime() {
            const second = getTimeSpentToday();
            const minute = (parseInt(second, 10) / 1000 / 60).toFixed(0);

            return {
                minute,
                second,
            };
        },
        getAppUseTimeTaskStatus(task, appUseTime) {
            if (appUseTime.second > task.time && task.status !== 'AWARD') {
                return 'FINISH';
            }

            return task.status;
        },
        isAdTask(task) {
            return task.category === this.adTaskCategory;
        },
    },
};
</script>

<style scoped>
.list {
    background-color: transparent;
}

.item {
    display: flex;
    align-items: center;
    box-shadow: 0 1px 13px 0 rgba(143, 143, 143, 0.1);
}

.item.Hairline::after {
    color: #f1f1f1;
    height: 2px;
}

.item + .item {
    margin-top: 10px;
}
</style>
