<template>
    <task-item
        :is-novice="isNovice"
        :novice-task-day="noviceTaskDay"
        :award-count="task.rewardString || awardCount"
        :award-type="awardType"
        :label="task.name"
        :tip="getTip(task)"
        :button-label="getLabel(task)"
        :button-status="task.status"
        @click="taskClick(task)">
        <task-item-tip
            class="tip"
            v-if="task.tips"
            :tip="task.tips" />
    </task-item>
</template>

<script>
import TaskItemTip from './TaskItemTip';
import TaskListItemMixin from './TaskListItemMixin';

export default {
    name: 'TaskListItem',
    mixins: [TaskListItemMixin],
    components: {
        TaskItemTip,
    },
    methods: {
        getTip() {
            switch (this.task.category) {
            case 8:
                return `已被 <span>${this.task.count}</span> 个好友阅读，每日晚上24点开奖`;
            case 9:
                return `收获 <span>${this.task.count}</span>/5 人评论可评为优质动态`;
            default:
                return '';
            }
        },
    },
};
</script>
<style scoped>
.tip {
    margin-top: 10px;
}
</style>
