import config from '@/config';
import request from '../api/request';
import { getUserAccountInfo, getImeiMd5 } from './jsApiV2/client';

/**
 * {
 *      page: '',
 *      logType: '',
 *      tagIdList: '', // 广告 id 逗号链接
 *      count: '', // 广告数量
 * }
 *
 * @author 文桥 <wenqiao1@xiaomi.com>
 * @export
 * @param {*} data
 */
export default function uploadNewsFeedLog(data) {
    const deviceInfo = config.deviceInfo;

    if (!deviceInfo.miui_version) {
        return;
    }

    const userAccountInfo = getUserAccountInfo();
    const imeiMd5 = getImeiMd5();
    const logDeviceInfo = {
        channel: deviceInfo.channel,
        appVersion: deviceInfo.version_name,
        miuiVersion: deviceInfo.miui_version,
        oaid: deviceInfo.oaid,
        imei: imeiMd5,
        userId: userAccountInfo.user_id,
    };
    const log = Object.assign({}, data, logDeviceInfo);

    request.spost('https://r.browser.miui.com/log/uploadNewsFeedLog', log);
}
