<template>
    <div class="rank">
        <div class="title">
            <span>想看财富榜</span>
            <a @click="goWithdrawGuide">快速提现攻略</a>
        </div>
        <div
            @click="goWithdrawRank"
            class="ranks"
        >
            <div
                class="item"
                v-for="(user, index) in rank"
                :key="user.id"
            >
                <div>
                    <div :class="['avator', `rank-avator-${index + 1}`]">
                        <ImageLoader
                            class="usericon"
                            :src="user.userIcon"
                            :backup-src="defaultUserIcon"
                            :c-style="{width: '100%', height: '100%', backgroundSize: 'contain'}"
                        />
                        <i :class="['rank-logo', `rank-logo-${index + 1}`]" />
                    </div>
                    <p class="money">累计{{ user.userCash }}元</p>
                </div>
            </div>
        </div>
        <a
            class="all Hairline Hairline_top"
            @click="goWithdrawRank"
        >查看全部</a>
    </div>
</template>

<script>
import config from '@/config';
import ImageLoader from '@/components/ImageLoader';
import { openCommonWebViewByPath, getUserAccountInfo } from '@/utils/jsApiV2/client';

export default {
    name: 'WithdrawRank',
    components: {
        ImageLoader,
    },
    props: {
        rank: { type: Array, default: () => [] },
    },
    methods: {
        goWithdrawGuide() {
            this.$o2o({
                e_c: '任务',
                e_a: '点击',
                e_n: '提现攻略点击',
                e_x: {
                    source: '任务中心',
                },
            });
            openCommonWebViewByPath('/mobile-v2/withdraw/quick-guide', '快速提现攻略');
        },
        goWithdrawRank() {
            this.$o2o({
                e_c: '财富榜',
                e_a: '点击',
                e_n: '赚金币页点击财富榜',
            });
            openCommonWebViewByPath('/news-v2/#page=withdrawRank', '财富排行榜');
        },
        goUserProfile(user) {
            try {
                const devinfo = config.deviceInfo;
                const userinfo = getUserAccountInfo();
                const versionCode = Number(devinfo.version_code);
                const defaultUser = {
                    id: user.userId || 0,
                    icon: user.userIcon || config.default.userIcon,
                    name: user.userName || '',
                };

                if (versionCode && versionCode >= 40100) {
                    this.$o2o({
                        e_c: '个人主页',
                        e_a: '曝光',
                        e_n: '个人主页曝光',
                        e_x: {
                            status: userinfo.user_id === user.userId ? 0 : 1,
                            source: 13,
                        },
                    });
                    const url = `bikan://goto/userInfoDetail?userId=${defaultUser.id}&headIcon=${encodeURIComponent(defaultUser.icon)}&nickName=${defaultUser.name}`;

                    console.log(url);
                    window.location.href = url;
                }
            } catch (error) {
                console.log(error);
            }
        },
    },
    data() {
        return {
            defaultUserIcon: config.default.userIcon,
        };
    },
};
</script>

