<template>
    <div
        ref="mark"
        class="layer"
        @click="handleClick"
    />
</template>

<script>
export default {
    name: 'MaskLayer',
    props: {
        click: { type: Function, required: true, default: () => {} },
    },
    methods: {
        handleClick() {
            this.click();
            this.$refs.mark.style.display = 'none';
        },
    },
};
</script>

