import request from '@/api/request';
import { base64EncodeUrlSafe } from '@/utils/string';

// https://wiki.n.miui.com/pages/viewpage.action?pageId=70107863
export default function stats(data, success, error) {
    return request.post('/ad-api/stats/track/js/v1.0', {
        value: base64EncodeUrlSafe(JSON.stringify(data)),
    })
        .then(success)
        .catch(error);
}
