<template>
    <div class="sign">
        <div class="sign__label">
            <p class="days">
                连续签到<span>{{ signedDays }}</span>天
            </p>
            <p class="award">
                再签<span>{{ getLastDay() }}</span>天可获得<span>{{ award }}</span>金币
            </p>
        </div>
        <Button
            :class="['sign__btn', { 'sign__btn-disabled': signed }]"
            @click="sign">
            {{ signed ? '已签到' : '签到' }}
        </Button>
    </div>
</template>

<script>
import Button from '@/components/Button';
import { signIn } from '@/utils/jsApiV2/act';
import config from '@/config';

const debounce = require('lodash.debounce');

export default {
    name: 'Sign',
    components: {
        Button,
    },
    props: {
        signedDays: { type: Number, default: 0 },
        award: { type: Number, default: 888 },
        signed: { type: Boolean, default: false },
    },
    methods: {
        sign: debounce(function sign() {
            signIn(String(this.signedDays || 0));

            this.$o2o({ e_c: '任务', e_a: '点击', e_n: '签到' });
        }, config.debounceWait),
        getLastDay() {
            return 7 - this.signedDays === 0 ? 7 : 7 - this.signedDays;
        },
    },
};
</script>

