<template>
    <button
        :class="getClassName(status)"
        v-on="$listeners">
        <span
            v-if="progress !== null"
            class="progress"
            :style="{ width: `${formattedProgress}%` }" />
        <span class="label">{{ label }}</span>
    </button>
</template>

<script>
export default {
    name: 'Button',
    props: {
        isNovice: { type: Boolean, default: false },
        label: { type: String, default: 'Button' },
        status: { type: String, default: 'START' },
        progress: { type: Number, default: null },
    },
    computed: {
        formattedProgress() {
            return this.progress > 100 ? 100 : this.progress;
        },
    },
    methods: {
        getClassName(status) {
            if (this.isNovice) {
                return {
                    btn: true,
                    start: status === 'START' || status === 'NOT_START',
                    finish: false,
                    award: status === 'FINISH',
                };
            }

            return {
                btn: true,
                start: status === 'START' || status === 'NOT_START',
                finish: status === 'FINISH',
                award: status === 'AWARD',
            };
        },
    },
};
</script>

<style scoped>
.btn {
    position: relative;
    height: 30px;
    width: 73px;
    font-size: 14px;
    border-radius: 15px;
    background-color: #dcdcdc;
    color: #fff;
    overflow: hidden;
    z-index: 1;
}

.label {
    position: relative;
    z-index: 1;
}

.start {
    border: 1px solid #fa5959;
    background-color: #fff;
    color: #f36152;
}

.finish {
    background-color: #fa5959;
    color: #fff;
}

.award {
    background-color: #dcdcdc;
    color: #fff;
}

.progress {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    background-color: #fdbcbc;
}
</style>
