import debounce from 'lodash.debounce';
import { award, noviceAward, adAward } from '@/api/task';
import {
    getUserAccountInfo,
    doLogin,
    signIn,
    toast,
} from '@/utils/jsApi';
import TaskItem from './TaskItem';

export default {
    components: {
        TaskItem,
    },
    props: {
        isNovice: { type: Boolean, default: false },
        isNewNoviceTask: { type: Boolean, default: false },
        noviceTaskDay: { type: Number, default: 1 },
        signedDays: { type: Number, default: 0 },
        awardCount: {
            type: [Number, String],
            default: 0,
        },
        awardType: {
            type: String,
            default: 'coin',
        },
        task: {
            type: Object,
            default: () => ({}),
        },
    },
    data() {
        return {
            taskLoginCallback: 'taskLoginCallback',
        };
    },
    computed: {
        // 4.6.7需适配两种新手任务，所以有两种判断条件
        isNewUser() {
            return this.isNovice || this.isNewNoviceTask;
        },
    },
    methods: {
        getAward({ task, isAd }) {
            let getAward;

            if (isAd) {
                getAward = adAward;
            } else if (this.isNewNoviceTask && task.noviceTaskType) {
                getAward = noviceAward;
            } else {
                getAward = award;
            }

            return getAward({
                task_id: task.id,
                // 新手任务没有sign字段，领取奖励不需要传sign
                ...(task.sign && { sign: task.sign }),
            }).then((res) => {
                if (res.data.status === 200) {
                    // 领取成功提示
                    try {
                        window.miui.customToast(
                            res.data.data.awardType,
                            res.data.data.toastTitle,
                            res.data.data[res.data.data.awardType.toLowerCase()],
                        );
                        window.miui.onReceiveCoinSuccess();
                    } catch (error) {
                        console.log(error);
                    }

                    this.emitAwardEvent(task.id);

                    return Promise.resolve();
                }

                // 领取失败提示
                toast(res.data.msg || '领取失败');

                return Promise.reject();
            }, (res) => {
                if (res.response && res.response.status === 401) {
                    window.miui.loginByPosition('otherTasks', '');
                }
                // 领取失败提示
                toast((res.data && res.data.msg) || '领取失败');

                return Promise.reject();
            });
        },
        getLabel(task) {
            let map;
            if (this.isNovice && !this.isNewNoviceTask) {
                map = {
                    NOT_START: task.buttonText,
                    START: task.buttonText,
                    FINISH: '已完成',
                    AWARD: '已完成',
                };
            } else {
                map = {
                    NOT_START: task.buttonText,
                    START: task.buttonText,
                    FINISH: '可领取',
                    AWARD: '已完成',
                };
            }

            const label = map[task.status] || '去完成';

            return label;
        },
        taskClick: debounce(function taskClick(task) {
            const isLogin = getUserAccountInfo().user_id;

            // 日常任务打点
            if (task.status !== 'FINISH' && !this.isNewUser) {
                this.$o2o({
                    e_c: '任务',
                    e_a: '点击',
                    e_n: '去完成',
                    e_x: {
                        task: task.name,
                        task_status: task.status === 'AWARD' ? 0 : 1,
                        login: isLogin ? 1 : 0,
                    },
                });
            }

            // 新手任务打点
            if (task.status !== 'FINISH' && this.isNewUser) {
                this.$o2o({
                    e_c: '新手任务',
                    e_a: '点击',
                    e_n: '去完成',
                    e_x: {
                        task: task.name,
                        task_day: this.noviceTaskDay,
                    },
                });
            }

            /*
             * 手动领取日常任务时长奖励
             * 手动领取新人任务奖励
             */
            if ((task.category === 1
                && task.status === 'FINISH'
                && task.time <= task.appUseTime.second)
                || (this.isNewNoviceTask
                    && task.status === 'FINISH')) {
                this.$o2o({
                    e_c: '任务',
                    e_a: '领取',
                    e_n: '任务点击领取',
                    e_x: {
                        task: task.name,
                    },
                });

                this.handleManualTask(isLogin, task);
                return;
            }

            if (task.category === 4) {
                console.log('signIn', this.signedDays);
                signIn(String(this.signedDays || 0));
                return;
            }

            this.handleNormalTask(isLogin, task);
        }, 200),
        emitAwardEvent(taskId) {
            this.$emit('award', taskId);
        },
        handleNormalTask(isLogin, task) {
            if (!isLogin) {
                doLogin();
            } else if (task.status !== 'AWARD') {
                location.href = task.link;
            }
        },
        handleManualTask(isLogin, task) {
            this.handleTask(isLogin, () => {
                this.getAward({ task });
            });
        },
        handleTask(isLogin, callback) {
            if (isLogin) {
                callback();
            } else {
                this.handleLogin(callback);
            }
        },
        handleLogin(callback) {
            window[this.taskLoginCallback] = callback;
            doLogin(this.taskLoginCallback);
        },
    },
};
