export function getWithdrawPageInfo(data = {}) {
    return request.get('/api/v4/exchange/withdraw/page', data)
        .then((response) => {
            if (response.data.status !== 200) {
                apiResError(response);

                return Promise.reject(response.data.data);
            }

            return Promise.resolve(response.data.data);
        });
}

/**
 * https://wiki.n.miui.com/pages/viewpage.action?pageId=132818620
 * 通用参数 && { type: 'wechat'|'phonenumber', phonenumber: 13423423423, cash: 1000 } && 用户登录相关 cookie
 *
 * 注：
 * 1. phonenumber 在 type 为 'phonenumber' 时必填
 * 2. cash 的单位为分
 */
export function post(data = {}) {
    return request.post('/api/v4/exchange/withdraw', data)
        .then((response) => {
            if (response.data.status !== 200 && response.data.status !== 2000) {
                // 未登录状态无需上报
                if (response.data.status !== 401) {
                    apiResError(response);
                }

                return Promise.reject(response.data);
            }

            return Promise.resolve(response.data.data || response.data);
        });
}

