/**
 * https://wiki.n.miui.com/pages/viewpage.action?pageId=206922921
 *
 * @author 文桥 <wenqiao1@xiaomi.com>
 * @export
 * @param {*} [data={}]
 * @returns
 */
export function getWithdrawRecord(data = {}) {
    return request.get('/api/v4/withdraw/list', data);
}

function wthdrawRecordModelFactory(data) {
    const { withdrawingRecord, recordList, nextPage } = data;
    const getWithdrawTypeLabel = (record) => {
        const withdrawMap = {
            0: '话费充值',
            1: '微信提现',
        };
        const withdraw = withdrawMap[record.withdrawType] || withdrawMap[1];
        const label = record.phoneNo
            ? `${withdraw}（${record.phoneNo}）`
            : withdraw;

        return label;
    };
    const getCash = record => (record.cash / 100).toFixed(2);
    let withdrawRecord = {
        empty: true,
    };

    if (withdrawingRecord) {
        withdrawRecord = {
            withdrawType: getWithdrawTypeLabel(withdrawingRecord),
            cash: getCash(withdrawingRecord),
            applyTime: format(withdrawingRecord.createTime, 'YYYY-MM-DD hh:mm'),
            releaseTime: format(withdrawingRecord.updateTime, 'YYYY-MM-DD hh:mm'),
            progress: [ // 当前订单只存在 10（审核中）/ 20（审核通过） 状态
                {
                    name: '已提交',
                    finished: withdrawingRecord.status === 10,
                },
                {
                    name: '待审核',
                    finished: withdrawingRecord.status === 10,
                },
                {
                    name: '审核通过',
                    finished: withdrawingRecord.status === 20,
                },
            ],
        };
    }
    const withdrawRecords = recordList.map(item => Object.assign(item, {
        withdrawType: getWithdrawTypeLabel(item),
        cash: getCash(item),
        applyTime: format(item.createTime, 'YYYY-MM-DD hh:mm'),
    }));
    const model = {
        withdrawRecord,
        withdrawRecords,
        nextPage,
    };

    return model;
}

export function wthdrawRecordModel(response) {
    if (response.data.status !== 200) {
        apiResError(response);

        return Promise.reject(response.data.data);
    }

    const model = wthdrawRecordModelFactory(response.data.data);
    return model;
}

