<template>
    <div class="novice-task">
        <TaskHeader
            class="novice-header"
            text="新手任务"
            :desc="desc">
            <div
                v-if="!isNewNoviceTask"
                class="novice-task__day">
                第<span>{{ noviceTaskDay }}</span>/{{ noviceTaskTotalDay }}天
            </div>
        </TaskHeader>
        <TaskList
            class="novice-list"
            :task="tasks"
            :signed-days="signedDays"
            :novice-task-day="noviceTaskDay"
            :is-novice="true"
            :is-new-novice-task="isNewNoviceTask" />
    </div>
</template>

<script>
/*
 * 为兼容新人任务和老5元任务，根据noviceTaskDay取值展示不同任务
 * 判断依据: isNewNoviceTask为真时展示新人任务，否则展示老5元任务
 */

import TaskHeader from './TaskHeader';
import TaskList from './TaskList';

export default {
    name: 'NoviceTask',
    components: {
        TaskHeader,
        TaskList,
    },
    props: {
        isNewNoviceTask: { type: Boolean, default: false },
        noviceTask: { type: Object, default: () => ({}) },
        noviceTaskDay: { type: Number, default: 1 },
        noviceTaskTotalDay: { type: Number, default: 5 },
        signedDays: { type: Number, default: 0 },
        noviceTaskLeftDays: { type: Number, default: 7 },
    },
    computed: {
        tasks() {
            return this.isNewNoviceTask
                ? this.noviceTask[0] : this.noviceTask[this.noviceTaskDay] || [];
        },
        desc() {
            return this.isNewNoviceTask ? `限时${this.noviceTaskLeftDays}天` : '完成后提现5元';
        },
    },
    created() {
        this.$o2o({
            e_c: '新手任务',
            e_a: '曝光',
            e_n: '5元解锁',
            e_x: {
                task_day: this.noviceTaskDay,
            },
        });
    },
};
</script>

<style scoped>
.novice-task {
    overflow: hidden;
    margin: 0;
}

div.novice-header {
    padding-top: 7px;
    padding-right: 7px;
    padding-left: 7px;
}

.novice-task__day {
    font-size: 15px;
    font-weight: 600;
    line-height: 21px;
}

.novice-task__day span {
    color: #eb4940;
}
</style>
