<template>
    <div class="task-item-tip">
        <div :class="['title', showTip ? 'open' : 'close']">
            <div @click.capture.stop="toggleTip">小技巧</div>
        </div>
        <div
            class="content"
            v-show="showTip">{{ tip }}</div>
    </div>
</template>

<script>
export default {
    name: 'TaskItemTip',
    props: {
        tip: {
            type: String,
            default: '',
        },
    },
    data() {
        return {
            showTip: false,
        };
    },
    methods: {
        toggleTip() {
            this.showTip = !this.showTip;
        },
    },
};
</script>

