import stats from '@/api/adStats';
import config from '@/config';

export default function adTracker(params) {
    const data = {
        H: {},
        B: [{
            H: {
                key: config.configKey,
                pk: config.packageName,
                eventTime: Date.now(),
            },
            B: {
                _category_: 'ad',
                _action_: params.type,
                ex: params.ex,
            },
        }],
    };

    stats(data);
}
