<template>
    <task-item
        ref="item"
        :is-ad="true"
        :is-novice="isNovice"
        :award-count="awardCount"
        :award-type="awardType"
        :label="adTask.appName || adTask.name"
        :tip="summary"
        :button-label="buttonLabel"
        :button-status="buttonStatus"
        :progress="downloadProgress"
        @click="handleAdClick(adTask)" />
</template>

<script>
/*
    广告任务组件
    按钮状态、操作依赖于应用商店返回的下载状态码（wiki: https://wiki.n.miui.com/pages/viewpage.action?pageId=139140507）
    以及自定义状态码：
    成功打开app状态码 200
    成功领奖状态码 201
*/

import debounce from 'lodash.debounce';
import config from '@/config';
import { addCustomEventListener, removeCustomEventListener } from '@/utils/event';
import {
    getUserAccountInfo,
    toast,
} from '@/utils/jsApiV2/client';
import {
    startDownloadApp,
    pauseDownloadApp,
    resumeDownloadApp,
    getDownloadResult,
} from '@/utils/jsApiV2/download';
import { openApp } from '@/utils/jsApiV2/act';
import { openDeepLink } from '@/utils/jsApiV2/deeplink';
import adTracker from '@/utils/adTracker';
import { exposeElement } from '@/utils/tracker';
import { set, remove } from '@/utils/cache';
import uploadNewsFeedLog from '@/utils/newsFeedLog';
import { cachedAdTaskKey } from './config';
import TaskListItemMixin from './TaskListItemMixin';

export default {
    name: 'TaskListAdItem',
    mixins: [TaskListItemMixin],
    data() {
        return {
            downloadStatus: 0,
            downloadProgress: 0,
            downloadMsg: '',
            downloadCallback: 'getDownloadResult',
            buttonLabelMap: Object.freeze({
                0: '下载',
                1: '开始下载',
                2: '下载完成',
                3: '开始安装',
                4: '打开应用',
                5: '正在下载',
                102: '打开应用',
                105: '继续下载',
                106: '安装中',
                200: '待领取',
                201: '已完成',
            }),
            buttonStatusMap: Object.freeze({
                4: 'FINISH',
                102: 'FINISH',
                200: 'FINISH',
                201: 'AWARD',
            }),
            adEventTypeMap: Object.freeze({
                1: 'APP_START_DOWNLOAD',
                2: 'APP_DOWNLOAD_SUCCESS',
                3: 'APP_INSTALL_START',
                4: 'APP_INSTALL_SUCCESS',
            }),
            appDownloadFailEventType: 'APP_DOWNLOAD_FAIL',
            appInstallFailEventType: 'APP_INSTALL_FAIL',
            appLaunchSuccessEventType: 'APP_LAUNCH_SUCCESS_PACKAGENAME',
            appLaunchFailEventType: 'APP_LAUNCH_FAIL_PACKAGENAME ',
            awardStatus: 'AWARD',
            installedStatusCode: 102,
            openSuccessStatusCode: 200,
            awardStatusCode: 201,
            cachedAdTask: null,
        };
    },
    computed: {
        buttonLabel() {
            return this.buttonLabelMap[this.downloadStatus] || '下载';
        },
        buttonStatus() {
            return this.buttonStatusMap[this.downloadStatus] || 'NOT_START';
        },
        adTask() {
            return this.cachedAdTask ? this.cachedAdTask : this.task;
        },
        summary() {
            return `下载并打开使用，立即领取${this.adTask.coin}金币`;
        },
    },
    watch: {
        downloadStatus(newVal) {
            // 负数状态码（除-1）时显示错误信息
            if (newVal < -1) {
                toast(this.downloadMsg);
            }

            if (!this.isMiAppPlayADStats()) {
                this.sendAdDownloadStats(newVal);
            }
        },
        adTask: {
            handler() {
                this.getInitDownloadResult(this.adTask);
            },
            deep: true,
            immediate: true,
        },
    },
    created() {
        uploadNewsFeedLog({
            page: 'TaskCenter',
            logType: 'adListType',
            tagIdList: this.adTask.tagId,
            count: 1,
        });
        uploadNewsFeedLog({
            page: 'TaskCenter',
            logType: 'adExpose',
            tagIdList: this.adTask.tagId,
            count: 1,
        });

        this.registerDownloadCallback();
        this.registerFocusHandler();
        this.registerBlurHandler();
    },
    destroyed() {
        this.unregisterFocusHandler();
        this.unregisterBlurHandler();
    },
    methods: {
        isMiAppPlayADStats() {
            const deviceInfo = config.deviceInfo;
            const version = Number(deviceInfo.version_code);

            if (version && version >= 40730) {
                return true;
            }

            return false;
        },
        registerDownloadCallback() {
            window[this.downloadCallback] = this.getDownloadResult;
        },
        registerFocusHandler() {
            addCustomEventListener('focus', this.validateDownloadResult);
        },
        unregisterFocusHandler() {
            removeCustomEventListener('focus', this.validateDownloadResult);
        },
        registerBlurHandler() {
            addCustomEventListener('blur', this.removeCachedAdTask);
        },
        unregisterBlurHandler() {
            removeCustomEventListener('blur', this.removeCachedAdTask);
        },
        // 目的是当用户在打开广告app前就删除app，当用户返回页面时重新获取广告app的下载状态，以更新按钮状态
        // 由于现有曝光打点需要通过滑动事件触发，当用户返回页面时页面可能会展示新广告，所以在这里手动触发一次曝光流程
        validateDownloadResult() {
            if (this.downloadStatus !== this.openSuccessStatusCode
            && this.downloadStatus !== this.awardStatusCode) {
                this.getInitDownloadResult(this.adTask);
            }

            exposeElement(this.$refs.item.$el, adTracker);
        },
        removeCachedAdTask() {
            const isLogin = getUserAccountInfo().user_id;

            // 未登录用户点击领取会调客户端登录接口，从而触发此方法，避免清除未登录用户之前看到的广告
            if (isLogin
            && this.cachedAdTask
            && this.downloadStatus === this.awardStatusCode) {
                this.cachedAdTask = null;
                remove(cachedAdTaskKey);

                this.resetDownloadResult();
                this.resetAdExpose();
            }
        },
        getInitDownloadResult(adTask) {
            if (this.downloadStatus === this.openSuccessStatusCode) return;

            const downloadResult = getDownloadResult(adTask);

            if (adTask.status === this.awardStatus
            && downloadResult.code === this.installedStatusCode) {
                this.setAwardStatus();
            } else if (downloadResult.code) {
                this.getDownloadResult(downloadResult);
            } else {
                this.resetDownloadResult();
            }
        },
        resetDownloadResult() {
            this.getDownloadResult({
                msg: '',
                code: 0,
                progress: 0,
            });
        },
        getDownloadResult(downloadResult) {
            this.downloadMsg = downloadResult.msg;
            this.downloadStatus = downloadResult.code;
            this.downloadProgress = downloadResult.progress;
        },
        sendAdDownloadStats(downloadStatus) {
            const eventType = this.getAdDownloadEventType(downloadStatus);

            this.sendAdStats(eventType);
        },
        getAdDownloadEventType(downloadStatus) {
            switch (true) {
            case (downloadStatus > 0 && downloadStatus < 5):
                return this.adEventTypeMap[downloadStatus];
            case (downloadStatus < 0 && downloadStatus !== -3):
                return this.appDownloadFailEventType;
            case (downloadStatus === -3):
                return this.appInstallFailEventType;
            default:
                return '';
            }
        },
        handleAdClick: debounce(function handler(adTask) {
            switch (this.downloadStatus) {
            case 0:
                this.startDownload(adTask);
                break;
            case 1:
            case 5:
                this.pauseDownload(adTask);
                break;
            case 4:
            case 102:
                this.openDownload(adTask);
                break;
            case 105:
                this.resumeDownload(adTask);
                break;
            case this.openSuccessStatusCode:
                this.getDownloadAward(adTask);
                break;
            case this.awardStatusCode:
            default:
                break;
            }
        }, config.debounceWait),
        startDownload(adTask) {
            // 返回任务页会重新请求新广告，需要缓存之前的广告任务，避免被覆盖
            this.cacheAdTask(adTask);

            startDownloadApp(adTask, this.downloadCallback);

            this.$emit('download');

            this.sendAdStats('CLICK');
        },
        pauseDownload(adTask) {
            pauseDownloadApp(adTask, this.downloadCallback);
        },
        openDownload(adTask) {
            if (!this.cachedAdTask) this.cacheAdTask(adTask);

            if (openDeepLink(adTask) || openApp(adTask)) {
                this.downloadStatus = this.openSuccessStatusCode;

                this.$emit('download');

                adTracker({
                    type: this.appLaunchSuccessEventType,
                    ex: adTask.ex,
                });
            } else {
                adTracker({
                    type: this.appLaunchFailEventType,
                    ex: adTask.ex,
                });
            }

            this.sendAdStats('CLICK');
        },
        resumeDownload(adTask) {
            resumeDownloadApp(adTask, this.downloadCallback);
        },
        getDownloadAward(adTask) {
            this.getAward({
                task: adTask,
                isAd: true,
            })
                .then(this.setAwardStatus)
                .catch(() => {});
        },
        sendAdStats(eventType) {
            if (!eventType) return;

            if (eventType === 'CLICK') {
                uploadNewsFeedLog({
                    page: 'TaskCenter',
                    logType: 'adClick',
                    tagIdList: this.adTask.tagId,
                    count: 1,
                });
            }

            adTracker({
                type: eventType,
                ex: this.adTask.ex,
            });
        },
        cacheAdTask(adTask) {
            this.cachedAdTask = adTask;
            set(cachedAdTaskKey, JSON.stringify(adTask));
        },
        setAwardStatus() {
            this.downloadStatus = this.awardStatusCode;

            this.$emit('finish');
        },
        // 由于新广告也需要曝光，调用此方法将之前的曝光状态置新
        resetAdExpose() {
            if (!this.$refs.item) return;

            const adElement = this.$refs.item.$el;

            adElement.dataset.isexpose = '0';
        },
    },
};
</script>

