<template>
    <van-swipe
        :class="{
            swipe: true,
            'novice-swipe': inNoviceTask
        }"
        :duration="500"
        :autoplay="2500"
        :show-indicators="false"
        :vertical="true">
        <van-swipe-item
            class="item"
            v-for="user in withdrawUsers"
            :key="user.userId"
        >
            {{ user.nickName }} 刚刚提现{{ (user.cash / 100) }}元
        </van-swipe-item>
    </van-swipe>
</template>

<script>
import * as withdraw from '@/api/withdraw';
import * as cache from '@/utils/cache';
import * as event from '@/utils/event';

export default {
    name: 'WithdrawSwipe',
    props: {
        inNoviceTask: {
            required: false,
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            withdrawUsers: [],
        };
    },
    methods: {
        getWithdrawUser(data) {
            withdraw.getWithdrawUsers(data).then((res) => {
                // 已经翻到最后一页了
                if (res.data.status === 200) {
                    if (res.data.data.length === 0 && data.page !== 1) {
                        this.setRechargeListPage(1);
                        return this.getWithdrawUser({ page: 1 });
                    }

                    this.withdrawUsers = res.data.data;
                    this.setRechargeListPage();
                }

                return false;
            });
        },
        setRechargeListPage(page) {
            cache.set('task__rechargeListPage', page || this.getRechargeListPage() + 1);
        },
        getRechargeListPage() {
            const page = cache.get('task__rechargeListPage');
            const expires = cache.get('task__rechargeListPage_expires');

            if (page && expires && Date.parse(expires) > Date.now()) {
                return parseInt(page, 10);
            }

            const now = new Date();

            cache.set(
                'task__rechargeListPage_expires',
                new Date(now.getFullYear(), now.getMonth(), now.getDate(), 23, 59, 59),
            );
            cache.set('task__rechargeListPage', 1);

            return 1;
        },
    },
    created() {
        const page = this.getRechargeListPage();

        this.getWithdrawUser({ page });

        // focus 时去请求下一页数据，页数累计，新一天重新计数
        event.addCustomEventListener('focus', () => {
            this.withdrawUsers = [];
            const nextPage = this.getRechargeListPage();

            this.getWithdrawUser({ page: nextPage });
        });
    },
};
</script>

<style scoped>
.swipe {
    height: 22px;
    width: 100%;
    overflow: hidden;
    background-color: rgba(255, 197, 197, 1);
}

.novice-swipe {
    background-color: #fb4b0d;
}

.item {
    height: 22px;
    line-height: 22px;
    text-align: center;
    font-size: 12px;
    color: rgba(255, 30, 30, 1);
    background-color: rgba(255, 197, 197, 1);
    overflow: hidden;
}

.novice-swipe .item {
    color: #fff;
    background-color: #fb4b0d;
}
</style>
