<template>
    <div
        :class="{
            task: !isShowNewHeader(),
            'task-v40600': isShowNewHeader(),
        }"
    >
        <header
            v-if="!isShowNewHeader()"
            :class="{
                header: true,
            }"
            :style="{paddingTop: getStatusBarHeight()}"
        >任务中心</header>
        <div v-if="isShowNewHeader()">
            <NavBar
                title="任务中心"
                class="header-v40600"
            />
        </div>
        <div
            :style="{
                paddingTop: -(31 + parseInt(getStatusBarHeight())) + 'px',
                paddingBottom: !isShowNewHeader()
                    ? ((31 + parseInt(getStatusBarHeight())) + 33) + 'px' : '0.1rem',
            }"
            :class="{
                'task-content-v40600': isShowNewHeader(),
                'task-overflow': !isShowNewHeader(),
            }"
        >
            <WithdrawSwipe />
            <div
                ref="taskBanner"
                :class="{
                    'no-normal-task-banner':
                        !(data.bannerList && data.bannerList.length),
                    'normal-task-banner':
                        (data.bannerList && data.bannerList.length)
                }"
            >
                <BannerSwipe
                    v-if="data.bannerList && data.bannerList.length"
                    class="task-banner"
                    :banners="data.bannerList"
                />
            </div>
            <div class="task__main">
                <!-- <Sign
                    class="task-sign"
                    :signed-days="data.signedDays"
                    :award="data.lastSignedDayAward"
                    :signed="data.signed"
                />
                <PigTask
                    v-if="isShowPigTask()
                    && data.pigTask && data.pigTask.needShow"
                    :day="data.pigTask.feedCount"
                    :status="data.pigTask.feedStatus"
                    class="pig"
                /> -->
                <!-- <TimeLimitPacket
                    v-if="isShowTimeLimitPacket()"
                /> -->
                <NoviceTask
                    v-if="data.inNoviceTask"
                    :is-new-novice-task="!data.isPreNoviceVersion"
                    :novice-task="data.noviceTaskList"
                    :novice-task-day="data.noviceTaskDay"
                    :signed-days="data.signedDays"
                    :novice-task-total-day="data.noviceTaskTotalDay"
                    :novice-task-left-days="data.noviceTaskLeftDays"
                />
                <NormalTask
                    v-if="data.taskList && data.taskList.length > 0"
                    :normal-task="data.taskList"
                />
            </div>
        </div>
    </div>
</template>

<script>
import * as task from '@/api/task';
import {
    getStatusBarHeight,
    getAdParameters,
} from '@/utils/jsApiV2/client';
import config from '@/config';
import { addCustomEventListener } from '@/utils/event';
import uploadNewsFeedLog from '@/utils/newsFeedLog';
import NavBar from '@/components/NavBar';
// import miVerify from '@/utils/miVerify';
import WithdrawSwipe from './WithdrawSwipe';
import NoviceTask from './NoviceTask';
import PigTask from './PigTask';
import Sign from './Sign';
// import TimeLimitPacket from './TimeLimitPacket';
import BannerSwipe from './BannerSwipe';
import WithdrawRank from './WithdrawRank';
import NormalTask from './NormalTask';
import MaskLayer from './MaskLayer';
// import '../../../static/v';

export default {
    name: 'Task',
    components: {
        NavBar,
        WithdrawSwipe,
        NoviceTask,
        Sign,
        PigTask,
        // TimeLimitPacket,
        BannerSwipe,
        WithdrawRank,
        NormalTask,
        MaskLayer,
    },
    data() {
        return {
            marked: false,
            loading: true,
            data: {},
            finishedNoviceTask: false,
            shouldRequestAd: true,
            adParameters: null,
        };
    },
    created() {
        uploadNewsFeedLog({
            page: 'TaskCenter',
            logType: 'enter',
            tagIdList: '',
            count: 0,
        });
        this.getAdParameters();

        this.init();

        // addCustomEventListener('focus', () => {
        //     console.log('focus', this.$refs.markArrow);
        //     if (this.$refs.markArrow) {
        //         this.$refs.markArrow.style.display = 'none';
        //     }
        // });

        addCustomEventListener('focus', () => {
            console.log('focus', 'init');
            this.init();
        });

        // 签到成功回调
        window.JSRegister.taskSignIn = () => {
            console.log(this, 'JSRegister.taskSignIn');
            this.init();
        };

        document.addEventListener('visibilitychange', () => {
            if (document.hidden) {
                this.shouldRequestAd = false;
            } else {
                this.shouldRequestAd = true;
            }
        });
    },
    methods: {
        isShowPigTask() {
            const deviceInfo = config.deviceInfo;
            const versionCode = Number(deviceInfo.version_code);

            return versionCode && versionCode >= 40400;
        },
        isShowTimeLimitPacket() {
            // const deviceInfo = config.deviceInfo;
            // const versionCode = Number(deviceInfo.version_code);

            return false;
        },
        isShowNewHeader() {
            const deviceInfo = config.deviceInfo;
            const versionCode = Number(deviceInfo.version_code);

            return versionCode && versionCode >= 40600;
        },
        getStatusBarHeight() {
            const ptpx = `${(getStatusBarHeight() / window.devicePixelRatio) + 12}px`;

            return ptpx;
        },
        showError() {
            this.$error(this.$route.name);
        },
        setData(data) {
            this.data = { ...data };
        },
        // mark() {
        //     if (!cache.get('noviceMark')) {
        //         // window.document.documentElement.style.overflow = 'hidden';
        //         this.marked = true;
        //         setTimeout(() => {
        //             if (this.$refs.markArrow) {
        //                 this.$refs.markArrow.style.display = 'block';
        //             }
        //         }, 0);
        //         cache.set('noviceMark', 1);
        //     } else {
        //         // window.document.documentElement.style.overflow = 'auto';
        //     }
        // },
        // markClick() {
        //     if (this.$refs.markArrow) {
        //         this.$refs.markArrow.style.display = 'none';
        //     }
        //     // window.document.documentElement.style.overflow = 'auto';
        // },
        setSwipeBannerHeight(ele, offset = 0) {
            if (!ele) {
                return;
            }
            setTimeout(() => {
                const rect = ele.getBoundingClientRect();

                ele.style.height = `${(rect.width - offset) * 0.29411764705882354}px`; // eslint-disable-line
            }, 0);
        },
        init() {
            uploadNewsFeedLog({
                page: 'TaskCenter',
                logType: 'sendRequest',
                tagIdList: '',
                count: 0,
            });
            task.get({
                ts: Date.now(),
                presentads: this.shouldRequestAd,
                ...this.adParameters,
            }).then((res) => {
                if (res.data.status === 200) {
                    this.setData(res.data.data);

                    if (res.data.data.bannerList && res.data.data.bannerList.length) { // eslint-disable-line
                        this.setSwipeBannerHeight(this.$refs.taskBanner);
                    }

                    this.$root.hidePlaceholder();
                    return;
                }
                this.showError();
            }).catch((e) => {
                console.error(e);
                this.showError();
            });
        },
        getAdParameters() {
            this.adParameters = getAdParameters();
        },
    },
};
</script>

