<template>
    <task-item
        :is-novice="isNovice && !isNewNoviceTask"
        :novice-task-day="noviceTaskDay"
        :award-count="task.rewardString || awardCount"
        :award-type="awardType"
        :label="task.name"
        :tip="getTip(task)"
        :button-label="getLabel(task)"
        :button-status="task.status"
        @click="taskClick(task)" />
</template>

<script>
import TaskListItemMixin from './TaskListItemMixin';

export default {
    name: 'TaskListItem',
    mixins: [TaskListItemMixin],
    methods: {
        getTip(task) {
            switch (task.category) {
            case 0:
                return `每日可完成 ${task.maxTimes} 次，今日已完成 <span>${task.maxTimes - task.leftTimes}</span> 次`;
            case 1:
                return `今日在线 <span>${task.appUseTime.minute}</span> 分钟`;
            case 2:
            case 4:
                return task.status === 'AWARD' || (this.isNovice && task.status === 'FINISH') ? '已完成' : '未完成';
            case 3:
                return `今日已完成${task.maxTimes - task.leftTimes}次`;
            case 6:
                return this.formatSubTitle(task);
            case 10:
            case 11:
            case 13:
                return this.formatSubTitle(task, task.maxTimes - task.leftTimes);
            case 12:
                return this.formatSubTitle(task, task.finishedDays);
            default:
                return task.subTitle;
            }
        },
        formatSubTitle(task, number = 0) {
            return task.subTitle.replace(/%s|{(\d+)}/, (match, p1) => ` <span>${p1 || number}</span> `);
        },
    },
};
</script>

